html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
  background-color: #171919;
  color: gray;
  text-transform: capitalize; }

body a {
  color: #fff; }

.container {
  width: 1920px;
  margin: 0 auto; }

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  display: inline-block;
  text-decoration: none; }

.headline-left {
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 7.33333px;
  text-transform: uppercase;
  color: #FFFFFF;
  opacity: 0.5;
  padding-left: 103px;
  position: relative;
  display: inline-block; }
  .headline-left::before {
    content: "";
    position: absolute;
    width: 80px;
    height: 1px;
    background-color: #FFFFFF;
    left: 0;
    top: 50%; }

.title span {
  font-weight: 800;
  font-size: 200px;
  color: rgba(255, 255, 255, 0.02);
  position: relative;
  display: inline-block;
  transform: translateX(-50px); }

.title h3 {
  position: absolute;
  right: 0;
  top: 0;
  margin: 0;
  color: #FFFFFF;
  font-size: 80px;
  line-height: 156px; }
  .title h3::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 80px;
    height: 1px;
    background-color: #4AF6CD; }

.subtitle {
  display: inline-block;
  text-align: right; }
  .subtitle__head {
    font-weight: 800;
    font-size: 40px;
    padding-right: 10px; }
    .subtitle__head span {
      font-weight: 100; }

.header {
  position: relative;
  height: 100vh; }

.header__slider-item {
  height: 100vh !important;
  background-image: url("../../build/img/header-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative; }

.slider-item__info {
  position: absolute;
  right: 80px;
  bottom: 115px;
  text-align: right; }

.slider-item__info-sutitle {
  margin-bottom: 5px; }

.slider-item__info-suptext {
  margin-bottom: 8px; }

.slider-item__info-title {
  font-weight: 800;
  font-size: 80px;
  line-height: 86px;
  margin-bottom: 48px; }

.slider-item__info-text {
  font-weight: 800;
  font-size: 40px;
  line-height: 48px;
  margin-bottom: 20px; }

.slider-arrows {
  position: absolute;
  top: 40px;
  right: 80px;
  cursor: pointer;
  z-index: 4; }

.slider-arrows__left {
  right: 125px; }

.slider-dots {
  position: absolute !important;
  right: 50px;
  bottom: 36px;
  max-width: 920px;
  width: 100%;
  z-index: 5;
  margin: 0; }
  .slider-dots .slick-current .dots-box {
    border-top: 4px solid #4AF6CD; }

.slick-track {
  transform: none !important; }

.dots-box {
  width: 200px;
  display: flex;
  justify-content: space-between;
  height: 28px;
  align-items: center;
  padding-top: 6px; }
  .dots-box__number {
    font-weight: 800;
    font-size: 24px;
    line-height: 34px; }
  .dots-box__name {
    font-weight: 300;
    line-height: 19px;
    text-transform: capitalize; }

.header__aside {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  height: 100vh;
  text-align: center; }

.header__title {
  font-weight: 800;
  font-size: 200px;
  line-height: 156px;
  opacity: 0.07;
  max-width: 467px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4; }

.menu {
  padding-top: 40px; }
  .menu a span {
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 3px;
    padding-top: 6px;
    margin-bottom: 20px;
    text-transform: capitalize; }

.header__date {
  font-weight: 800;
  font-size: 72px;
  line-height: 64px; }
  .header__date span {
    padding-top: 15px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 1.4px;
    margin-bottom: 30px;
    display: block; }

.header__search {
  padding-top: 15px;
  padding-bottom: 35px; }

.header__location {
  display: inline-block;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1.05px;
  position: relative;
  padding-left: 19px;
  text-transform: capitalize; }
  .header__location::before {
    content: "";
    position: absolute;
    width: 11px;
    height: 13px;
    background-image: url("../../build/img/map-marker.svg");
    left: 0;
    top: 0;
    z-index: 5; }

.header__map {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3; }

.header__arrows {
  position: absolute;
  left: 210px;
  bottom: 40px;
  z-index: 3; }

.line-animation {
  stroke-dashoffset: 600;
  stroke-dasharray: 600;
  animation: animdash 4s infinite 5s; }

@keyframes animdash {
  to {
    stroke-dashoffset: 0; } }

.surf {
  background-image: url("../../build/img/surf-bg.png");
  background-position: right 200px;
  background-repeat: no-repeat;
  background-size: auto;
  padding: 160px 0 210px; }

.surf__subtitle {
  text-align: right;
  padding-right: 80px;
  margin-top: -86px; }

.surf-box {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover !important;
  min-height: 750px; }

